@import url(https://fonts.googleapis.com/css?family=Russo+One);
body {
  background: black;
  margin: 10px 0;
}

::-webkit-scrollbar-corner {
  border-radius: 2px;
  background: red;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  border-radius: 20px;
  background: #2c2b2b;
}

::-webkit-scrollbar-thumb {
  background: #990202;
  border-radius: 20px;
  width: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: red;
  border-radius: 20px;
  width: 10px;
}

#preview, .title {
  display: inline-block;
}

.colorScheme, .editorWrap textarea, .previewWrap {
  background-color: #1a1a1a;
  border: 1px solid black;
  border-radius: 10px;
  box-shadow: 1px 1px 10px 2px black;
  border-top: none;
  color: rgba(255, 255, 255, 0.877);
}

.toolbar {
  position: relative;
  background-color: #f50000;
  padding: 4px 4px 3px 3px;
  border: 1px solid black;
  border-radius: 10px;
  box-shadow: 1px 1px 10px 2px black;
  font-family: Russo One;
  font-size: 15px;
}

.toolbar i {
  width: 25px;
  color: black;
  margin-left: 5px;
}

.toolbar .fa-expand,
.toolbar .fa-compress {
  position: absolute;
  right: -5px;
  font-size: 22px;
  margin-right: 5px;
}

.fa-retweet:hover,
.fa-compress:hover,
.fa-expand:hover {
  color: #ff7575;
  cursor: pointer;
  font-size: 22px;
  margin-right: 5px;
}

.fa-file-code {
  margin-right: 3px;
  font-size: 22px;
}

#container {
  display: flex;
  margin: 10px auto;
}

.editorWrap {
  position: fixed;
  z-index: 1;
  width: 400px;
  margin: 30px;
}

.editorWrap .toolbar {
  width: 99%;
}

.editorWrap textarea {
  width: 99%;
  height: 500px;
  min-height: 200px;
  margin-bottom: -5px;
  resize: vertical;
  outline: none;
  padding-left: 5px;
  padding-top: 5px;
  font-size: 15px;
}

.converter {
  width: 100px;
  text-align: center;
  font-size: 35px;
  margin: auto;
}

.previewWrap {
  width: 800px;
  margin: 30px auto;
  min-height: 200px;
  overflow-wrap: break-word;
}

.previewWrap .toolbar {
  width: 100%;
  padding-right: 17px;
  border-right: 0px;
  border-left: 0px;
}

.previewWrap #preview {
  margin-left: 5px;
  margin-top: -10px;
  width: 100%;
}

@media screen and (max-with: 850px) {
  .previewWrap {
    width: 630px;
    display: block;
  }
  .editorWrap {
    width: 550px;
    display: block;
  }
}

.maximized {
  width: 96%;
  min-height: 100vh;
  margin: 10px auto;
}

.maximized textarea {
  min-height: 90vh;
  resize: none;
}

.hide {
  display: none;
}

#preview blockquote {
  border-left: 3px solid #a3c705;
  color: #fc7a00;
  padding-left: 5px;
  margin-left: 25px;
}

#preview code {
  background: #868383;
  padding: 1px 4px 2px 4px;
  font-size: 15px;
}

#preview pre {
  background: #868383;
  padding: 5px 0 5px 5px;
  margin-right: 10px;
}

#preview h1 {
  border-bottom: 2px solid #db0000;
  width: 99%;
}

#preview h2 {
  border-bottom: 1px solid #db0000;
  width: 99%;
}

#preview table {
  border-collapse: collapse;
}

#preview td, #preview th {
  border: 2px solid #db0000;
  padding-left: 5px;
  padding-right: 5px;
}

@media screen and (max-width: 1400px) {
  #container {
    margin-right: auto;
    margin-left: auto;
  }
  .previewWrap {
    margin-top: 290px;
    margin-right: auto;
    margin-left: -10px;
  }
  .editorWrap {
    margin: 10px auto;
    border-bottom: 2px solid white;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .editorWrap textarea {
    height: 200px;
  }
}

.maximized1 {
  width: 96%;
  min-height: 100vh;
  margin: 10px auto;
  margin-left: -120px;
  padding-top: 0px;
}

.maximized1 textarea {
  min-height: 95vh;
  resize: none;
}

.maximized1 img {
  width: 350px;
  margin-left: 10px;
}
/*# sourceMappingURL=index.css.map */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

